.container {
  width: 100%;
  background: linear-gradient(
    to bottom,
    var(--white-color) 0%,
    var(--white-color) 50%,
    var(--light-gray-color) 50%,
    var(--light-gray-color) 100%
  );
}

.form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0 2rem 0 2rem;
  background-color: var(--dark-violet-color);
  border-radius: 0.5rem;
}

.input {
  padding: 1rem 2rem;
  margin: 1rem 2rem 0rem 2rem;
  border-radius: 0.5rem;
  border: none;
}

.inputError {
  color: var(--red-color);
  border: 1px solid var(--red-color);
}

.errorMessage {
  margin: 1rem 2rem 0rem 2rem;
  color: var(--red-color);
}

.button {
  display: block;
  background-color: var(--cyan-color);
  color: var(--white-color);
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  margin: 1rem 2rem;
  cursor: pointer;
}

.button:hover,
.button:active {
  background-color: var(--cyan-tint-color);
}

.button:disabled {
  cursor: default;
  background-color: var(--cyan-tint-color);
}

.progressMessage {
  align-self: center;
  color: var(--white-color);
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .form {
    margin: 0 4rem 0 4rem;
  }
}
